<template>
  <div>
    <v-card class="pb-4" height="60">
      <div class="mt-5 mx-5">
        <template v-if="$auth.check(['ADMIN', 'RESPONSIBLE'])">
          <v-btn class="mr-2 my-2" @click="create()" color="#007bff" dark>
            <v-icon left>mdi-arrow-decision-outline</v-icon>
            {{ $vuetify.lang.t("$vuetify.btn.add") }}
          </v-btn>
        </template>
        <v-btn
          :loading="loading.refresh"
          @click="refreshItems()"
          class="mr-2 my-2"
          depressed
          color="#f1f5fc"
        >
          <v-icon left color="#007bff">mdi-refresh</v-icon>
          {{ $vuetify.lang.t("$vuetify.btn.refresh") }}
        </v-btn>
        <v-btn
          color="#f1f5fc"
          @click="showFilter()"
          class="mr-2 my-2"
          depressed
        >
          <v-icon color="#007bff" left>mdi-filter-outline</v-icon>
          {{ $vuetify.lang.t("$vuetify.expenses_nature.btn.filter.title") }}
        </v-btn>
        <v-spacer></v-spacer>
      </div>
    </v-card>
    <v-card>
      <v-data-table
        :footer-props="{
          'items-per-page-options': itemPerPageOptions
        }"
        :headers="headers"
        :items="items"
        :items-per-page="itemPerPage"
        :loading="loading.list"
        :options.sync="pagination"
        :server-items-length="meta.totalElements"
        :sort-by="sortBy"
        :sort-desc="sortDesc"
        item-key="name"
        class="mt-5"
      >
        <template v-slot:header.nature_code="{ header }">
          <tr>
            {{
              $vuetify.lang.t(header.text)
            }}
          </tr>
        </template>
        <template v-slot:header.type="{ header }">
          <tr>
            {{
              $vuetify.lang.t(header.text)
            }}
          </tr> </template
        ><template v-slot:header.name="{ header }">
          <tr>
            {{
              $vuetify.lang.t(header.text)
            }}
          </tr>
        </template>
        <!--<template v-slot:header.nature="{ header }">-->
        <!--<tr>-->
        <!--{{-->
        <!--$vuetify.lang.t(header.text)-->
        <!--}}-->
        <!--</tr>-->
        <!--</template>-->
        <template v-slot:header.account_number="{ header }">
          <tr>
            {{
              $vuetify.lang.t(header.text)
            }}
          </tr>
        </template>
        <template v-slot:header.create_date="{ header }">
          <tr>
            {{
              $vuetify.lang.t(header.text)
            }}
          </tr>
        </template>
        <template v-slot:header.null="{ header }">
          <tr>
            {{
              $vuetify.lang.t(header.text)
            }}
          </tr>
        </template>
        <template v-slot:body="{ items }">
          <v-hover>
            <tbody slot-scope="{ hover }">
              <tr :key="item.id" v-for="item in items">
                <td class="subtitle-2">{{ item.code }}</td>
                <td class="subtitle-2">
                  {{ item.type }}
                </td>
                <td class="subtitle-2">
                  {{ item.name }}
                </td>
                <!--<td class="subtitle-2">-->
                <!--{{ item.nature }}-->
                <!--</td>-->
                <td class="subtitle-2">
                  {{ item.account_number }}
                </td>
                <td class="subtitle-2">
                  {{ $moment(item.createdAt).format("YYYY-MM-DD HH:mm:ss") }}
                </td>
                <td
                  class="subtitle-2"
                  v-if="$auth.check(['ADMIN', 'RESPONSIBLE'])"
                >
                  <template>
                    <v-tooltip bottom>
                      <template
                        v-slot:activator="{ on }"
                        v-if="$auth.check(['ADMIN', 'RESPONSIBLE'])"
                      >
                        <v-btn
                          @click="editUser(item)"
                          color="secondary"
                          icon
                          text
                          v-on="on"
                          v-show="$vuetify.breakpoint.smAndDown || hover"
                        >
                          <v-icon>mdi-account-edit</v-icon>
                        </v-btn>
                      </template>
                      <span>{{ $vuetify.lang.t("$vuetify.btn.edit") }}</span>
                    </v-tooltip>
                    <v-tooltip top>
                      <template
                        v-slot:activator="{ on }"
                        v-if="
                          (item.role === 'COMMERCIAL' ||
                            item.role === 'TELEOPERATOR') &&
                            $auth.check([
                              'SUPER_ADMINISTRATOR',
                              'SUPERVISOR',
                              'ADMINISTRATOR'
                            ])
                        "
                      >
                        <v-icon
                          @click="assignEnterprise(item)"
                          color="secondary"
                          v-on="on"
                          dark
                          v-show="$vuetify.breakpoint.smAndDown || hover"
                        >
                          mdi-plus-circle-outline
                        </v-icon>
                      </template>
                      <span>
                        {{ $vuetify.lang.t("$vuetify.btn.assign") }}
                      </span>
                    </v-tooltip>
                    <v-tooltip bottom>
                      <template
                        v-slot:activator="{ on }"
                        v-if="$auth.check(['ADMIN', 'RESPONSIBLE'])"
                      >
                        <v-btn
                          @click="deleteItem(item)"
                          color="red"
                          icon
                          text
                          v-on="on"
                          v-show="$vuetify.breakpoint.smAndDown || hover"
                          dark
                        >
                          <v-icon>mdi-delete-outline</v-icon>
                        </v-btn>
                      </template>
                      <span>{{ $vuetify.lang.t("$vuetify.btn.remove") }}</span>
                    </v-tooltip>
                  </template>
                </td>
                <td class="subtitle-2" v-else></td>
              </tr>
            </tbody>
          </v-hover>
        </template>
      </v-data-table>
      <form-user
        :dialog="dialogForm"
        :form="form"
        :messages="messages"
        @getItems="getItems"
        ref="userForm"
      ></form-user>
      <delete-item
        :dialog="dialogDelete"
        :messages="messagesDelete"
        :urlItems="urlDeleteItems"
        @getItems="getItems"
      ></delete-item>
      <filter-form
        ref="filterForm"
        :dialog="dialogForm"
        :filter="filter"
        @performFilter="performFilter"
      ></filter-form>
      <form-enable
        :dialog="dialogForm"
        ref="enableUser"
        :messages="messages"
        @getItems="getItems"
      ></form-enable>
    </v-card>
  </div>
</template>

<script>
import ListMixin from "./../../mixins/Common/List.vue";
import FormUser from "./Form";
import FilterForm from "./Filter";
import DeleteItem from "./../Common/Delete";

export default {
  mixins: [ListMixin],
  data: () => ({
    urlItems: "/expenses_nature",
    sortBy: ["id"],
    dialogForm: {
      show: false,
      shows: {
        showFilter: false,
        showInfo: false
      }
    },
    dialogDelete: {
      show: false
    },
    dialogDetail: {
      show: false
    },
    dialogEnt: {
      show: false
    },
    form: {
      id: null,
      type: null,
      email: null,
      nature_code: null,
      password: null,
      role: null,
      is_active: null
    },
    filter: {
      nature_code: null,
      role: null
    },
    messages: {
      title: "",
      submit: "",
      success: ""
    },
    messagesDelete: {
      success: "$vuetify.expenses_nature.delete.success"
    }
  }),
  methods: {
    setHeaders() {
      this.headers = [
        {
          text: "$vuetify.expenses_nature.cols.nature_code.title",
          value: "nature_code",
          align: "start",
          sortable: false,
          width: "15",
          class: "grey--text text--darken-3"
        },
        {
          text: "$vuetify.expenses_nature.cols.type.title",
          value: "type",
          align: "start",
          sortable: false,
          width: "50",
          class: "grey--text text--darken-3"
        },
        {
          text: "$vuetify.expenses_nature.cols.name.title",
          value: "name",
          align: "start",
          sortable: false,
          width: "50",
          class: "grey--text text--darken-3"
        },
        // {
        //   text: "$vuetify.expenses_nature.cols.nature.title",
        //   value: "nature",
        //   align: "start",
        //   sortable: false,
        //   width: "20",
        //   class: "grey--text text--darken-3"
        // },
        {
          text: "$vuetify.expenses_nature.cols.account_number.title",
          value: "account_number",
          align: "start",
          sortable: false,
          width: "30",
          class: "grey--text text--darken-3"
        },

        {
          text: "$vuetify.expenses_nature.cols.created.title",
          value: "create_date",
          align: "start",
          sortable: false,
          width: "105",
          class: "grey--text text--darken-3"
        },
        {
          text: "$vuetify.expenses_nature.cols.actions.title",
          value: null,
          align: "start",
          sortable: false,
          width: "140"
        }
      ];
    },
    create() {
      this.dialogForm.show = true;
      this.messages = {
        title: "$vuetify.expenses_nature.new.title",
        submit: "$vuetify.expenses_nature.new.submit",
        success: "$vuetify.expenses_nature.new.success"
      };
      this.form = {
        id: null,
        type: null,
        name: null,
        nature: null,
        account_number: null,
        nature_code: null,
        is_active: false
      };
    },
    editUser(item) {
      this.dialogForm.show = true;
      this.messages = {
        title: "$vuetify.expenses_nature.update.title",
        submit: "$vuetify.expenses_nature.update.submit",
        success: "$vuetify.expenses_nature.update.success"
      };
      this.$refs.userForm.setForm(item);
    },
    showFilter() {
      this.dialogForm.shows.showFilter = true;
    },
    performFilter() {
      let self = this;
      self.loading.list = true;
      let params = {};
      if (self.filter.nature_code !== null) {
        params["nature_code"] = self.filter.nature_code;
      }
      if (self.filter.role !== null) {
        params["role"] = self.filter.role;
      }
      self._search = params;
      //console.log("FILTRE", this._search);
      self
        .getItems()
        .then(() => {
          self.$refs.filterForm.closeDialog();
        })
        .finally(() => {
          self.$refs.filterForm.stopLoadingBtn();
        });
    },
    assignEnterprise(item) {
      this.dialogEnt.show = true;
      this.$refs.assignForm.setForm(item);
    }
  },
  components: {
    FormUser,
    DeleteItem,
    FilterForm
  }
};
</script>

<style scoped></style>
